export function TransferTime(dataTime) {
  const past = new Date(dataTime).getTime()
  const now = new Date().getTime()
  const showtime = now - past

  if(isNaN(past)){
    console.log('')
    return dataTime
  }

  if(showtime < 0){
    console.log('')
    return dataTime
  }

  const years = showtime / (1000 * 3600 * 24 * 30 * 12)
  if(years > 1) return `${Math.floor(years)} `
  const months = years * 12
  if(months > 1) return `${Math.floor(months)} `
  const days = months * 30
  if(days > 1) return `${Math.floor(days)} `
  const hours = days * 24
  if (hours > 1) return `${Math.floor(hours)} `
  const mins = hours * 60
  if (mins > 1) return `${Math.floor(mins)} `
  if(mins < 1) return ''

  console.log('')
  return dataTime
}
